@import url('https://fonts.googleapis.com/css?family=Barlow:400,700,800&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Barlow', sans-serif;
    background-color: #ecf0f1;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

.content{
    text-align: center;
}

.title {
    font-size: 4rem;
    font-weight: 800;
    color: #1abc9c;
    margin-bottom: 20px;
    .img-title {
        width: 5%;
    }
}

.box {
    background-color: rgb(245, 245, 245);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    padding: 20px;
    margin: 20px;
    border-radius: 10px;
    box-shadow: 1px 1px 1px rgba($color: #000000, $alpha: 0.2);

    i{
        margin-top: 20px;
    }

    .box-1 {
        background-color: rgba(129, 236, 236,1.0);
        transition: 500ms ease-in-out;
       
        &:hover{
            transform: scale(1.2);
            i{
                animation: icon 3s ease-in-out infinite;
            }
        }
    }
    
    .box-2 {
        background-color: rgba(255, 234, 167,1.0);
        transition: 500ms ease-in-out;
        &:hover{
            transform: scale(1.2);
            i{
                animation: icon 3s ease-in-out infinite;
            }
        }
    }
    .box-3 {
        background-color: rgba(250, 177, 160,1.0);
        transition: 500ms ease-in-out;
        &:hover{
            transform: scale(1.2);
            i{
                animation: icon 3s ease-in-out infinite;
            }
        }
    }

    @keyframes icon {
        0%{
            transform: translateY(0px);
        }
       25%{
            transform: translateY(-10px);
        }
        50%{
            transform: translateY(0px);
        }
        75%{
            transform: translateY(-10px);
        }
        100%{
            transform: translateY(0px);
        }
    }


    .box-1,.box-2,.box-3 {
        width: 100%;
        height: 400px;
        cursor: pointer;
        border-radius: 5px;
        padding: 10px;
        display: flex;
        flex-direction: column;
        text-align: center;

        h2{
            margin: 20px 0 50px 0;
            text-transform: uppercase;
        }
        span{
            font-weight: 800;
            font-size: 2rem;
        }

        .date-week {
            font-size: 1rem;
            margin-bottom: 20px;
            font-weight: 400;
        }
    }
}

.select-div{
    position: relative;
    display: flex;
    margin: 0 auto;
    width: 60%;
    height: 3em;
    line-height: 3;
    background: #FFFFFF;
    box-shadow: 0 1px 4px #cccccc;
    overflow: hidden;
    border-radius: .25em;
    
    select {
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;
        outline: 0;
        box-shadow: none;
        border: 0 !important;
        background: #FFFFFF;
        background-image: none;
      }

      select {
        flex: 1;
        font-size: 1.2rem;
        padding: 0 .5em;
        color: rgb(36, 36, 36);
        cursor: pointer;
      }

      &::after {
        content: '\25BC';
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 1em;
        background: rgb(245, 245, 245);
        cursor: pointer;
        pointer-events: none;
        -webkit-transition: .25s all ease;
        -o-transition: .25s all ease;
        transition: .25s all ease;
      }
}

footer {
    a{
        text-decoration: none;
    }
}

// responsive css

@media (max-width: 980px) {
    .title {
        font-size: 3rem;
    }
    .box-1,.box-2,.box-3 {
        h2{
            font-size: 1.3rem;
        }
    }
}

@media (max-width: 680px) {
    .content {
        overflow: auto;
    }
    .title {
        font-size: 1.5rem;
        margin: 10px 0 0 0;
    }
    .box-1,.box-2,.box-3 {
        h2{
            font-size: 1.3rem;
        }
    }
    
    .box {
        grid-template-columns: 1fr;

        .box-1,.box-2,.box-3 {
            width: 100%;
            height: 200px;
            h2{
                margin: 6px 0 0px 0;
    
            }
            span{
                font-weight: 800;
                font-size: 1.5rem;
            }
    
            .date-week {
                font-size: 1rem;
                margin-bottom: 5px;
                font-weight: 400;
            }
        }
        i{
            margin-top: 10px;
        }
    } 
    footer{
        margin-bottom: 20px;
    }

    .select-div {
        width: 65%;
        height: 2em;
        margin-top: 10px;
    }
    .select-div select {
        font-size: 1rem;
    }
    .select-div::after {
        top: -8px;
    }
}


